$sudoku-dark: #675e73;
$sudoku-light: #8b7c99;

* {
    font-family: 'Mulish', 'Muli', sans-serif;
    // font-size: 1vw;
    font-weight: 500;

	outline: 1px dotted #888;
}

body {
    display: flex;
    flex-direction: column;
    width: 100vw; height: 100vh;
}

h1 {
    font-size: 3rem;
    font-weight: 800;
}

.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.space-between {
    padding: 1vw 3vw;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}

header {
    height: 12.5vw;

    background-color: #f88;
}

.main-buttons {
    width: 30vw;

    ul {
        display: flex;
        padding: 0; margin: 0;
        list-style-type: none;
        gap: 2vw;
    }

    &.left ul {
        display: flex;
        align-items: flex-start;
    }

    &.right ul {
        display: flex;
        align-items: flex-end;
    }
}

.title-area {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.setter {
    /* Maybe this needs a different font size. Not sure. */
}

main {
    height: 100%;
}

.left-panel {
    padding: 1vw 3vw 1vw 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    height: 100%;
    width: 20vw;

    > ul >li::before {
        content: "â–¾ ";
        /* This can't be the right way to do this, can it? The intent is to have a drop-down tree menu. */
    }

    * {
        padding: 0;
        list-style-type: none;
    }

    form > div {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    background-color: #8f8
}

.right-panel {
    height: 100%;
    width: 20vw; height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    background-color: #88f;
}

.sudoku {
    display: flex;
    align-items: center;
    justify-content: center;

    /* maintain square aspect ratio */
    width: 70vh; height: 70vh;

    color: white;
    background-color: $sudoku-dark;
}

footer {
    width: 100vw;
    height: 3vw;

    font-size: 10px;
}
